/* eslint-disable no-labels */
import React, { Component } from "react";
import Navbar from "./Navbar.js";
import WelcomeImg from "../img/logo512.png";
import Footer from "./Footer.js";
import Swal from "sweetalert2";
import MetaTags from "react-meta-tags";
import { CarBrandList } from "./CarBrandList";
import CarBrandImages from "./CarBrandImages";
import AutoPartsForm from "./AutoPartForm";
import Car from "../img/greencar.png";
import AutoPartsCategory from "./AutoPartsCategory.js";

export default class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      CarBrandList: CarBrandList,
    };
  }
  submituserInquiryForm() {
    var carbattery = document.getElementById("carbattery").value;
    var area = document.getElementById("area").value;
    var contactno = document.getElementById("contactno").value;
    var submitted = true;

    if (carbattery === "") {
      submitted = false;
      document.getElementById("carbatteryerror").innerHTML =
        "** Please fill the Battery Model";
      //return false;
    } else if (area === "") {
      submitted = false;
      document.getElementById("areaerror").innerHTML =
        "** Please fill the Area";
      //return false;
    } else if (contactno === "") {
      submitted = false;
      document.getElementById("contactnoerror").innerHTML =
        "** Please fill the Contact";
      //return false;
    } else if (isNaN(contactno)) {
      submitted = false;
      document.getElementById("contactnoerror").innerHTML =
        "** Check Your contact no";
      //return false;
    } else {
      if (submitted) {
        Swal.fire("Form submitted. We will contact you shortly ;)").then(() =>
          document.getElementById("myForm").onreset(window.location.reload())
        );
        return submitted;
      }
    }
  }

  render() {
    return (
      <div>
        <Navbar />
        <MetaTags>
          <title>
            AutoB - Used / New / Genuine / Aftermarket Auto Spare Parts in Dubai
            and Sharjah , UAE
          </title>
          <meta charset="utf-8" />
          <meta
            name="title"
            content="Used / New / Genuine / Aftermarket Auto Spare Parts in Dubai and Sharjah - AutoB"
          />
          <link rel="canonical" href="https://emirates-car.com" />
          <meta
            name="description"
            content="AutoB - We arrange Auto spare parts for almost all brands including most popular brands such as BMW, audi, maserati, maruti, nano, tata, rolls royce etc.. at best rate at dubai and sharjah, UAE. We also provide car battery replacement. Our well trained professionals are 24/7 supportive for any road side assisstance anywhere at dubai and sharjah."
          />
        </MetaTags>
        <div className="container-fluid text-center pt-2">
          <h2 className="mj-heading">Car parts and Accessories at Dubai {"&"} Sharjah</h2>
          <p className="paragraph">
            It's good to trust the parts, it's better to trust the hands who
            give you the parts.
          </p>
        </div>
        <div className="container">
          <div className="d-flex justify-content-center header-paragraph-carousel py-5">
            <div
              id="carouselExampleControls"
              class="carousel vert slide "
              data-ride="carousel"
              data-interval="1500"
            >
              <div class="carousel-inner">
                <div class="carousel-item active">
                  <p>Car Auto Spare parts for {"=>"}</p>
                </div>
                <div class="carousel-item">
                  <p>American Car Brands Auto Parts</p>
                </div>
                <div class="carousel-item">
                  <p>Japnese Car Brands Auto Parts</p>
                </div>
                <div class="carousel-item">
                  <p>German Car Brands Auto Parts</p>
                </div>
                <div class="carousel-item">
                  <p>Korean Car Brands Auto Parts</p>
                </div>
                <div class="carousel-item">
                  <p>French Car Brands Auto Parts</p>
                </div>
                <div class="carousel-item">
                  <p>British Car Brands Auto Parts</p>
                </div>
                <div class="carousel-item">
                  <p>Indian Car Brands Auto Parts</p>
                </div>
                <div class="carousel-item">
                  <p>Chinese Car Brands Auto Parts</p>
                </div>
                <div class="carousel-item">
                  <p>Heavy vehicles Auto Parts</p>
                </div>
              </div>
            </div>
            <img
              src={Car}
              alt="car battery replacement sharjah"
              className="img-fluid header-car-img"
            />
          </div>
        </div>

        {/*Welcome message*/}
        <div className="container py-5">
          <div className="row text-center justify-content-center">
            <img
              src={WelcomeImg}
              className="img-fluid rounded-circle welcomeImg"
              alt="car battery replacement sharjah"
              style={{ backgroundColor: "#FF4500" }}
            />
            <h1 className="container heading">Welcome to AutoB !</h1>
            <p className="paragraph">
              We at AutoB are dedicated arrange AUTO SPARE PARTS for any any
              vehicles including japanese car brands, american car brands,
              indian car brands, german car brands, korean car brands, french
              car brands, british car brands, heavy vehicles.
            </p>
            <div className="social-icons-container pt-3">
              <a
                href="https://api.whatsapp.com/send?phone=971551478994"
                rel="noopener noreferrer"
              >
                <i class="fab fa-whatsapp social-icons"></i>{" "}
              </a>
              <a href="https://www.facebook.com/mjautobattery/">
                <i class="fab fa-facebook social-icons"></i>{" "}
              </a>
              <a href="/">
                <i class="fab fa-instagram social-icons"></i>{" "}
              </a>
            </div>
          </div>
        </div>
        <div className="container py-5">
          <div className="row">
            <div className="col-md-6">
              <div
                className="text-center"
                style={{ borderLeft: "1px solid lightgray" }}
                id="auto_spare_parts"
              >
                <p className="paragraph">
                  Looking for auto spare parts in dubai and sharjah? We arrange
                  spare parts for almost all brands from brakes to gearbox, from
                  axle and rear axle and many more , available as Genuine Market
                  Product as well as Aftermarket Product. We provide service for
                  you with best perfection and quality results. Contact us
                  through whatsapp or fill the following forms. We will get back
                  to you.
                </p>
              </div>
            </div>
            <div className="col-md-6" id="car_battery_form">
              <AutoPartsForm />
            </div>
          </div>
        </div>
        <div className="container pt-5">
          <h1 className="heading">AutoB | Why you need us?</h1>
        </div>
        <div className="container-fluid pb-5 ">
          <div className="row d-flex justify-content-center ">
            <div className="col-md-3">
              <div className="card mx-1 card-img-width">
                <div className="card-body text-center">
                  <i
                    className="fa fa-check-circle fa-5x"
                    aria-hidden="true"
                  ></i>
                  <h5 className="text-center paragraph">Warranty Guaranteed</h5>
                </div>
              </div>
            </div>
            <div className="col-md-3">
              <div className="card mx-1 card-img-width">
                <div className="card-body text-center">
                  <i className="fas fa-money-bill fa-5x" aria-hidden="true"></i>
                  <h5 className="text-center paragraph">Easy payment</h5>
                </div>
              </div>
            </div>
            <div className="col-md-3">
              <div className="card mx-1 card-img-width">
                <div className="card-body text-center">
                  <i className="fa fa-wrench fa-5x" aria-hidden="true"></i>
                  <h5 className="text-center paragraph">
                    Professional Service
                  </h5>
                </div>
              </div>
            </div>
          </div>
          <div className="row d-flex justify-content-center">
            <div className="col-md-3">
              <div className="card mx-1 card-img-width">
                <div className="card-body text-center">
                  <i className="fa fa-phone fa-5x" aria-hidden="true"></i>
                  <h5 className="text-center paragraph">Quick response</h5>
                </div>
              </div>
            </div>
            <div className="col-md-3">
              <div className="card mx-1 card-img-width">
                <div className="card-body text-center">
                  <i className="fas fa-clock fa-5x" aria-hidden="true"></i>
                  <h5 className="text-center paragraph">24/7 Support</h5>
                </div>
              </div>
            </div>
            <div className="col-md-3">
              <div className="card mx-1 card-img-width">
                <div className="card-body text-center">
                  <i
                    className="fas fa-car-battery fa-5x paragraph1-icon"
                    aria-hidden="true"
                  ></i>
                  <h5 className="text-center paragraph">OEM Batteries</h5>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="container py-5">
          <div className="row">
            <div className="col-md-12 text-center">
              <h1 className="heading">
                GET AUTO SPARE PARTS {"&"} CAR BATTERY FOR ALMOST ALL MAKES
              </h1>
              <p className="paragraph">
                We provide car battery replacement services and we arrange auto
                spare parts for almost all brands in dubai and sharjah, united
                arab Emirates.
              </p>
              <CarBrandImages images={this.state.CarBrandList} />
            </div>
          </div>
        </div>
        <div className="container py-5">
          <div className="row">
            <div className="col-md-12 text-center">
              <h1 className="heading">AUTO SPARE PARTS</h1>
              <p className="paragraph">
                We arrange Auto spare parts for almost all brands including most
                popular brands such as BMW, audi, maserati, maruti, nano, tata,
                rolls royce etc.. at best rate at dubai and sharjah, UAE.
              </p>
              <AutoPartsCategory />
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}
