import React, { Component } from "react";
import "react-lazy-load-image-component/src/effects/opacity.css";
import { Link } from "react-router-dom";
import { CarBrandList } from "./CarBrandList";

export default class CarBrandImages extends Component {
  constructor(props) {
    super(props);
    this.state = {
      CarBrandList: CarBrandList,
    };
  }
  render() {
    return this.state.CarBrandList.map((image) => {
      return (
        <Link to="/contact">
          <img
            alt={image.name}
            className="brandImg"
            src={image.logo}
          />
        </Link>
      );
    });
  }
}
